@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

.object-contain{
    transform: scale( 1.7);
    border-radius : 20px
}


/*Start Animations*/
@-webkit-keyframes animatetop {
	from {
		top: -300px;
		opacity: 0;
	}
	to {
		top: 0;
		opacity: 1;
	}
}
@keyframes animatetop {
	from {
		top: -300px;
		opacity: 0;
	}
	to {
		top: 0;
		opacity: 1;
	}
}
@-webkit-keyframes zoomIn {
	0% {
		opacity: 0;
		-webkit-transform: scale3d(0.3, 0.3, 0.3);
		transform: scale3d(0.3, 0.3, 0.3);
	}
	50% {
		opacity: 1;
	}
}
@keyframes zoomIn {
	0% {
		opacity: 0;
		-webkit-transform: scale3d(0.3, 0.3, 0.3);
		transform: scale3d(0.3, 0.3, 0.3);
	}
	50% {
		opacity: 1;
	}
}

.area {
	background: #d7e240;
	background: -webkit-linear-gradient(to left, #84a738, #cbec39);
	width: 100%;
	height: 100vh;
	position: absolute;
	z-index: -1;
}

.circles {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 96%;
	overflow: hidden;
}

.circles li {
	position: absolute;
	display: block;
	list-style: none;
	width: 20px;
	height: 20px;
	background: rgba(255, 255, 255, 0.2);
	animation: animate 25s linear infinite;
	bottom: -150px;
}

.circles li:nth-child(1) {
	left: 25%;
	width: 80px;
	height: 80px;
	animation-delay: 0s;
}

.circles li:nth-child(2) {
	left: 10%;
	width: 20px;
	height: 20px;
	animation-delay: 2s;
	animation-duration: 12s;
}

.circles li:nth-child(3) {
	left: 70%;
	width: 20px;
	height: 20px;
	animation-delay: 4s;
}

.circles li:nth-child(4) {
	left: 40%;
	width: 60px;
	height: 60px;
	animation-delay: 0s;
	animation-duration: 18s;
}

.circles li:nth-child(5) {
	left: 65%;
	width: 20px;
	height: 20px;
	animation-delay: 0s;
}

.circles li:nth-child(6) {
	left: 75%;
	width: 110px;
	height: 110px;
	animation-delay: 3s;
}

.circles li:nth-child(7) {
	left: 35%;
	width: 150px;
	height: 150px;
	animation-delay: 7s;
}

.circles li:nth-child(8) {
	left: 50%;
	width: 25px;
	height: 25px;
	animation-delay: 15s;
	animation-duration: 45s;
}

.circles li:nth-child(9) {
	left: 20%;
	width: 15px;
	height: 15px;
	animation-delay: 2s;
	animation-duration: 35s;
}

.circles li:nth-child(10) {
	left: 85%;
	width: 150px;
	height: 150px;
	animation-delay: 0s;
	animation-duration: 11s;
}

@keyframes animate {
	0% {
		transform: translateY(0) rotate(0deg);
		opacity: 1;
		border-radius: 0;
	}

	100% {
		transform: translateY(-1000px) rotate(720deg);
		opacity: 0;
		border-radius: 50%;
	}
}

/* Glassmorphism styles */
.glass {
    background: rgba(255, 255, 255, 0.05);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.hover-scale {
    transition: all 0.3s ease-in-out;
}

.hover-scale:hover {
    transform: scale(1.05);
}

/* Smooth transitions */
.transition-all {
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 300ms;
}

/* Skills Card Animations */
@keyframes expandCard {
  from {
    height: 4rem;
  }
  to {
    height: auto;
  }
}

.skills-card-expanded {
  animation: expandCard 0.5s ease-out forwards;
}

/* Smooth height transitions */
.transition-height {
  transition: height 0.5s ease-in-out;
}

/* MacBook Dock Effect for Technology Icons */
.dock-effect {
  padding: 0.5rem 0;
}
.dock-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(30, 30, 30, 0.8);
  border-radius: 1rem;
  box-shadow: 0 4px 16px rgba(0,0,0,0.4);
  transition: transform 0.2s cubic-bezier(.25,.8,.25,1), box-shadow 0.2s;
  font-size: 2.5rem;
  width: 3.5rem;
  height: 3.5rem;
  margin: 0 0.1rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
}
.dock-icon:hover,
.dock-icon:focus {
  transform: scale(1.15) translateY(-6px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.5);
  z-index: 2;
}
.dock-icon:active {
  transform: scale(1.05) translateY(-2px);
}

/* Modern minimal glassy custom scrollbar styles */
::-webkit-scrollbar {
  width: 10px;
  background: rgba(30,30,30,0.08);
}
::-webkit-scrollbar-thumb {
  background: rgba(68,73,80,0.35);
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.10);
  backdrop-filter: blur(2px);
  -webkit-backdrop-filter: blur(2px);
  border: 1px solid rgba(255,255,255,0.08);
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(85,91,98,0.45);
}
::-webkit-scrollbar-track {
  background: rgba(24,24,27,0.12);
}

/* Firefox */
html {
  scrollbar-width: thin;
  scrollbar-color: rgba(68,73,80,0.35) rgba(24,24,27,0.12);
}

html, body {
  overflow-y: scroll !important;
}

/* Vapor effect animations */
@keyframes textShadowPulse {
  0% {
    text-shadow: 0 0 20px rgba(34, 211, 238, 0.2);
  }
  50% {
    text-shadow: 0 0 40px rgba(34, 211, 238, 0.8);
  }
  100% {
    text-shadow: 0 0 20px rgba(34, 211, 238, 0.2);
  }
}

@keyframes progressBar {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}

.animate-progress {
  animation: progressBar 3s ease-in-out forwards;
  transform-origin: left;
}

/* Hide scrollbar utility */
.scrollbar-hide {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.scrollbar-hide::-webkit-scrollbar {
  display: none;
}
